<template>
  <div class="coupon">
    <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
      <van-tab v-for="(item,index) in tabList" :key="index" :title="item.text"></van-tab>
    </van-tabs>
    <div class="mescroll coupon-list no-list" id="couponList">
      <div id="couponListWrap">
        <div class="coupon-list-item" v-for="(couponItem,index) in couponList" :key="index" @click="choose(couponItem)">
          <div class="coupon-list-item-left">
            <p class="title">
              <!-- <span :style="couponItem.coupon_state!=1?'background:#d8d8d8;':''">洗衣券</span> -->
              {{couponItem.ytitle}}
            </p>
            <p class="time">有效期至：{{String(couponItem.endDate).substring(0,10)}}</p>
            <p class="text">{{couponItem.status=='0'?'可使用':couponItem.status=='1'?'已使用':'已过期'}}
              <!-- | {{couponItem.coupon_note}} -->
            </p>
          </div>
          <div class="coupon-list-item-right">
            <p class="price">
              {{couponItem.ymoney}}
              <span>元</span>
            </p>
            <p class="text">满{{couponItem.yamt}}可用</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs } from "vant";
import MeScroll from "mescroll.js";
import { getCouponList } from "@/api";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      tabList: [
        {
          text: "全 部",
          value: ""
        },
        {
          text: "未使用",
          value: "0"
        },
        {
          text: "已使用",
          value: "1"
        },
        {
          text: "已过期",
          value: "9"
        }
      ],
      mescroll: null,
      activeType: 0,
      active: 0,
      couponList: []
    };
  },

  methods: {
    onClick(name, title) {
      this.activeType = this.tabList[name].value;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    choose(item) {
      if (this.$route.query.type == "1") {
        if (!(item.status == "0" && item.yamt <= this.$route.query.money)) {
          this.$toast("优惠券不可用");
          return;
        }
        sessionStorage.setItem("couponDetail", JSON.stringify(item));
        this.$router.back();
      }
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let dataObj = {
        pagesize: pageSize,
        pagenum: pageNum,
        status: this.activeType
      };
      getCouponList(dataObj)
        .then(res => {
          console.log(res.data);
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            this.$toast.fail(res.msg);
          }
        })
        .catch(err => {
          errorCallback && errorCallback();
          console.log(err);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("couponList", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        down: {
          htmlContent:
            '<p class="downwarp-progress" ></p><p class="downwarp-tip">下拉刷新 </p>'
        },
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "couponListWrap",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          scrollbar: { use: false },
          htmlLoading:
            '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip" >加载中..</p>',
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>'
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let that = this;
      that.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          if (page.num == 1) that.couponList = [];
          //更新列表数据
          that.couponList = that.couponList.concat(curPageData.list);
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          that.mescroll.endBySize(curPageData.list.length, curPageData.count); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          that.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "优惠券";
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.coupon {
  height: 100%;
  .acitive {
    font-size: 15px;
    font-weight: bold;
  }
  &-list {
    &-item {
      margin: 15px;
      border-radius: 5px;
      display: flex;

      background: #fff;
      padding: 15px;
      box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
      &-left {
        flex: 2;
        .title {
          height: 25px;
          font-weight: bold;
          span {
            background: $base-color;
            color: #fff;
            font-size: 11px;
            display: inline-block;
            height: 25px;
            line-height: 25px;
            width: 45px;
            text-align: center;
            border-radius: 5px;
            margin-right: 5px;
          }
        }
        .time {
          line-height: 25px;
          font-size: 15px;
          margin-top: 10px;
          color: #000;
        }
        .text {
          font-size: 12px;
          color: #999;
          margin-top: 15px;
        }
      }
      &-right {
        text-align: center;
        .price {
          font-size: 25px;
          font-weight: bold;
          span {
            font-size: 15px;
          }
        }
        .text {
          font-size: 15px;
        }
      }
    }
  }
}
.no {
  &-list {
    height: calc(100% - 46px);
    background: #efefef;
  }
}
</style>
<style lang="scss">
</style>
